    <pre>
      <!-- <div>{{ itemsInTracks }}</div> -->
      <!-- <div>{{ itemsInTracks[0].length }}</div>
      <div>{{ itemsInTracks[1].length }}</div>
      <div>{{ itemsInTracks[2].length }}</div> -->
      <!-- <div>{{ caobaoPos.h }} {{ gameStatus }}</div> -->
    </pre>
  </div>
</template>

<script>
// import bgInit from '@/assets/images/bg_init.jpg'
import caobao1 from '@/assets/images/caobao-1.svg'
import caobao2 from '@/assets/images/caobao-2.svg'
import startText from '@/assets/images/fadacai-text.png'
import keyUp from '@/assets/images/key-up.svg'
import keyDown from '@/assets/images/key-down.svg'
import life from '@/assets/images/life.svg'
import score from '@/assets/images/score.svg'
import rock from '@/assets/images/rock.svg'
import mosquito from '@/assets/images/mosquito.svg'
import guoDon from '@/assets/images/guoDon.svg'
import tsaiIngWen from '@/assets/images/tsaiIngWen.svg'
import svg1450 from '@/assets/images/1450.svg'
import flooding from '@/assets/images/flooding.svg'
import star from '@/assets/images/star.svg'
import gold from '@/assets/images/gold.svg'
import mushroom from '@/assets/images/mushroom.svg'
import starCrash from '@/assets/images/star-crash.svg'
import goldCrash from '@/assets/images/gold-crash.svg'
import mushroomCrash from '@/assets/images/mushroom-crash.svg'
import sky from '@/assets/images/sky.png'
import skyWin from '@/assets/images/sky-win.png'
import cloud from '@/assets/images/cloud.png'
import cloudWin from '@/assets/images/cloud-win.png'
import road from '@/assets/images/road.png'
import city from '@/assets/images/city.png'
import starAurora from '@/assets/images/star-aurora.png'
import starShining from '@/assets/images/star-shining.png'
import gameover from '@/assets/images/gameover.svg'
import finish from '@/assets/images/finish.svg'
import fadacai from '@/assets/images/fadacai.svg'
import warning from '@/assets/images/warning.svg'
import ribbon from '@/assets/images/ribbon.svg'
import caobaoWin from '@/assets/images/caobao-win.svg'
const gamePanel = {
  showTime: 90,
  time: 90,
  score: 0,
  life: 3,
  mushroom: false,
  starTime: 0
}
const goal = [
  {
    type: 'finish',
    size: 30,
    pos: 0,
    speed: .7,
    stopPos: 0
  },
  {
    type: 'fadacai',
    size: 30,
    pos: 0,
    speed: .65,
    stopPos: 'center'
  }
]
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data() {
    return {
      caoBaoWinJump: {
        up: true,
        value: 0
      },
      winEffect: 0,
      failEffect: 0,
      itemInterval: null,
      zawarudo: false,
      mousePos: {
        w: 0, h: 0
      },
      gamePanel: {...gamePanel},
      imagesImport: {
        bg: {
          sky: sky,
          skyWin: skyWin,
          cloud: cloud,
          cloudWin: cloudWin,
          road: road,
          city: city
        },
        ribbon: ribbon,
        warning: warning,
        goal: {
          finish: finish,
          fadacai: fadacai
        },
        gameover: gameover,
        starAurora: starAurora,
        starShining: starShining,
        caobao: [caobao1, caobao2],
        caobaoWin: caobaoWin,
        startText: startText,
        keyUp: keyUp,
        keyDown: keyDown,
        life: life,
        score: score,
        dangers: {
          rock: rock,
          mosquito: mosquito,
          guoDon: guoDon,
          tsaiIngWen: tsaiIngWen,
          'svg1450': svg1450,
          flooding: flooding
        },
        items: {
          mushroom: mushroom,
          gold: gold,
          star: star,
          mushroomCrash: mushroomCrash,
          goldCrash: goldCrash,
          starCrash: starCrash
        }
      },
      images: {
        bg: {
          sky: null,
          skyWin: null,
          cloud: null,
          cloudWin: null,
          road: null,
          city: null
        },
        ribbon: null,
        warning: null,
        goal: {
          finish: null,
          fadacai: null
        },
        gameover: null,
        starAurora: null,
        starShining: null,
        caobao: [],
        caobaoWin: null,
        startText: null,
        keyUp: null,
        keyDown: null,
        life: null,
        score: null,
        dangers: {
          rock: null,
          mosquito: null,
          guoDon: null,
          tsaiIngWen: null,
          'svg1450': null,
          flooding: null
        },
        items: {
          mushroom: null,
          gold: null,
          star: null,
          mushroomCrash: null,
          goldCrash: null,
          starCrash: null
        }
      },
      fps: 200,
      fadacai: null,
      ctx: null,
      ww: 0,
      wh: 0,
      road: {
        speed: 1.5,
        pos1: 0,
        pos2: 0
      },
      cloud: {
        speed: 0,
        pos1: 0,
        pos2: 0
      },
      sky: {
        speed: .1,
        pos1: 0,
        pos2: 0
      },
      city: {
        speed: .2,
        pos1: 0,
        pos2: 0
      },
      caobaoRunnung: 0,
      tracks: [.6, .75, .9],
      caobaoPos: {
        w: 0, h: 0, currentTrack: 1
      },
      caobaoSize: {
        w: 0, h: 0, scale: 1, origin: 1, big: 1.2
      },
      gameStatus: 0,  // 0: start, 1: countdown start, 2: playing, 3: finish 4: failed
      itemSpeed: 1,
      golds: [],
      dangersLv1: [
        {
          p: 20,
          type: 'rock',
          danger: true,
          size: 5,
          pos: 0,
          speed: 1.25 // 相對道路的速度
        },
        {
          p: 15,
          type: 'guoDon',
          danger: true,
          size: 12,
          pos: 0,
          speed: 1.45
        },
        {
          p: 10,
          type: 'tsaiIngWen',
          danger: true,
          size: 12,
          pos: 0,
          speed: 1.6
        },
        {
          p: 15,
          type: 'svg1450',
          danger: true,
          size: 9,
          pos: 0,
          speed: 1.2
        }
      ],
      dangersLv2: [
        {
          p: 5,
          type: 'mosquito',
          danger: true,
          size: 5,
          pos: 0,
          speed: 1.8
        }
      ],
      dangersLv3: [
        {
          p: 2,
          type: 'flooding',
          danger: true,
          size: 30,
          pos: 0,
          speed: 2
        }
      ],
      itemsInTracks: [
        [],
        [],
        []
      ],
      items: [
        {
          p: 50,
          type: 'gold',
          size: 6,
          pos: 0,
          speed: 1
        },
        {
          p: 3,
          type: 'star',
          size: 6,
          pos: 0,
          speed: 1
        },
        {
          p: 6,
          type: 'mushroom',
          size: 6,
          pos: 0,
          speed: 1
        }
      ],
      goal: [],
      itemsInTrack1: [],
      itemsInTrack2: [],
      itemsInTrack3: []
    }
  },
  mounted() {
    this.initCanvas()
    setInterval(this.update, 1000 / this.fps)
    window.requestAnimationFrame(this.draw)
    window.addEventListener('keydown', this.onKeyboardClick)
    // window.addEventListener('mousemove', this.onMouseMove)
  },
  watch: {
    'gamePanel.time': function() {
      if(this.gamePanel.time <= 0) {
        this.gameStatus = 3
      }
    },
    'gamePanel.showTime': function() {
      // console.log(this.gamePanel.showTime)
      if(this.gamePanel.showTime <= 2) {
        clearInterval(this.itemInterval)
        this.goal = [...goal]
      }
    },
    'gamePanel.life': function() {
      if(this.gamePanel.life <= 0) {
        // console.log('123456')
        this.gameStatus = 4
      }
    }
  },
  methods: {
    initCanvas() {
      let canvas = this.$refs['fadacai']
      this.fadacai = this.$refs['fadacai']
      // 1919 x 1080
      let ww = window.innerWidth
      let wh = window.innerHeight - 30
      if (ww / wh >= 1919 / 1080) {
        ww = wh * 1919 / 1000
      } else {
        wh = ww * 1000 / 1919
      }
      canvas.width  = ww
      canvas.height  = wh
      this.ww = ww
      this.wh = wh
      let ctx =  canvas.getContext('2d')
      this.ctx = ctx

      // import bg
      this.images.bg.cloud = new Image()
      this.images.bg.cloud.src = this.imagesImport.bg.cloud
      this.images.bg.cloudWin = new Image()
      this.images.bg.cloudWin.src = this.imagesImport.bg.cloudWin
      this.images.bg.sky = new Image()
      this.images.bg.sky.src = this.imagesImport.bg.sky
      this.images.bg.skyWin = new Image()
      this.images.bg.skyWin.src = this.imagesImport.bg.skyWin
      this.images.bg.city = new Image()
      this.images.bg.city.src = this.imagesImport.bg.city
      this.images.bg.road = new Image()
      this.images.bg.road.src = this.imagesImport.bg.road

      // import 韓導
      let caobao1 = new Image()
      caobao1.src = this.imagesImport.caobao[0]
      this.images.caobao.push(caobao1)
      let caobao2 = new Image()
      caobao2.src = this.imagesImport.caobao[1]
      this.images.caobao.push(caobao2)
      this.caobaoSize.h = this.wh / 3.5
      this.caobaoSize.w = this.caobaoSize.h * 251.5 / 297.1
      this.caobaoPos.w = this.ww / 20,
      this.caobaoPos.h = this.wh - this.caobaoSize.h
      // improt win 草包 caobaoWin
      this.images.caobaoWin = new Image()
      this.images.caobaoWin.src = this.imagesImport.caobaoWin

      // import 韓導靈氣
      this.images.starAurora = new Image()
      this.images.starAurora.src = this.imagesImport.starAurora
      this.images.starShining = new Image()
      this.images.starShining.src = this.imagesImport.starShining

      // import 開始畫面
      this.images.startText = new Image()
      this.images.startText.src = this.imagesImport.startText

      // import key up/down hint
      this.images.keyUp = new Image()
      this.images.keyUp.src = this.imagesImport.keyUp

      this.images.keyDown = new Image()
      this.images.keyDown.src = this.imagesImport.keyDown

      // import life
      this.images.life = new Image()
      this.images.life.src = this.imagesImport.life

      // import score
      this.images.score = new Image()
      this.images.score.src = this.imagesImport.score

      // import rock 74.38 * 60.19
      this.images.dangers.rock = new Image()
      this.images.dangers.rock.src = this.imagesImport.dangers.rock

      // import mosquito 116.97 * 142.64
      this.images.dangers.mosquito = new Image()
      this.images.dangers.mosquito.src = this.imagesImport.dangers.mosquito

      // import guoDon 262.154 * 310.016
      this.images.dangers.guoDon = new Image()
      this.images.dangers.guoDon.src = this.imagesImport.dangers.guoDon

      // import tsaiIngWen 265.568 * 299.873
      this.images.dangers.tsaiIngWen = new Image()
      this.images.dangers.tsaiIngWen.src = this.imagesImport.dangers.tsaiIngWen

      // import svg1450 200.855 * 318.707
      this.images.dangers.svg1450 = new Image()
      this.images.dangers.svg1450.src = this.imagesImport.dangers.svg1450

      // import flooding 820.706 * 267.4
      this.images.dangers.flooding = new Image()
      this.images.dangers.flooding.src = this.imagesImport.dangers.flooding

      // import star crash 120.39 * 120.061
      this.images.items.star = new Image()
      this.images.items.star.src = this.imagesImport.items.star
      this.images.items.starCrash = new Image()
      this.images.items.starCrash.src = this.imagesImport.items.starCrash

      // import mushroom crash 117.608 * 98.783
      this.images.items.mushroom = new Image()
      this.images.items.mushroom.src = this.imagesImport.items.mushroom
      this.images.items.mushroomCrash = new Image()
      this.images.items.mushroomCrash.src = this.imagesImport.items.mushroomCrash

      // import gold crash 86.123 * 86.123
      this.images.items.gold = new Image()
      this.images.items.gold.src = this.imagesImport.items.gold
      this.images.items.goldCrash = new Image()
      this.images.items.goldCrash.src = this.imagesImport.items.goldCrash

      // import gameover
      this.images.gameover = new Image()
      this.images.gameover.src = this.imagesImport.gameover

      // import finish 612.149 * 435.515
      this.images.goal.finish = new Image()
      this.images.goal.finish.src = this.imagesImport.goal.finish
      // import fadacai 699.123 * 401.5
      this.images.goal.fadacai = new Image()
      this.images.goal.fadacai.src = this.imagesImport.goal.fadacai

      // import warning 1103.91 * 164.913
      this.images.warning = new Image()
      this.images.warning.src = this.imagesImport.warning
      // import ribbon 820.435 * 472.88
      this.images.ribbon = new Image()
      this.images.ribbon.src = this.imagesImport.ribbon

      this.ctx.save()

      goal[1].pos = this.ww / 3
    },
    update() {
      if(this.gameStatus === 2) {
        this.road.pos1 -= this.road.speed
        this.road.pos2 = this.road.pos1 + this.ww * 2
        if(this.road.pos1 <= -this.ww * 2) {
          this.road.pos1 = 0
          this.road.pos2 = this.road.pos1 + this.ww * 2
        }
        this.sky.pos1 -= this.sky.speed
        this.sky.pos2 = this.sky.pos1 + this.ww * 2
        if(this.sky.pos1 <= -this.ww * 2) {
          this.sky.pos1 = 0
          this.sky.pos2 = this.sky.pos1 + this.ww * 2
        }
        this.cloud.pos1 -= this.cloud.speed
        this.cloud.pos2 = this.cloud.pos1 + this.ww * 1.6
        if(this.cloud.pos1 <= -this.ww * 1.6) {
          this.cloud.pos1 = 0
          this.cloud.pos2 = this.cloud.pos1 + this.ww * 1.6
        }
        this.city.pos1 -= this.city.speed
        this.city.pos2 = this.city.pos1 + this.ww * 2
        if(this.city.pos1 <= -this.ww * 2) {
          this.city.pos1 = 0
          this.city.pos2 = this.city.pos1 + this.ww * 2
        }
        // 開始倒數
        if(this.gamePanel.time > 0) {
          this.gamePanel.time -= 1 / this.fps
        }
        if(this.gamePanel.time > 0) {
          this.gamePanel.showTime = Math.floor(this.gamePanel.time)
        }
        this.updateItems()
      }

      if(this.gameStatus === 3 || this.gameStatus === 4) {
        clearInterval(this.itemInterval)
      }

      if(this.gamePanel.mushroom === true) {
        if (this.caobaoSize.scale <= this.caobaoSize.big) {
          this.caobaoSize.scale += 0.01
        }
      } else {
        if (this.caobaoSize.scale >= this.caobaoSize.origin) {
          this.caobaoSize.scale -= 0.01
        }
      }
      if(this.gameStatus === 3) {
        if(this.caoBaoWinJump.up) {
          this.caoBaoWinJump.value += 1
          if(this.caoBaoWinJump.value >= 60) {
            this.caoBaoWinJump.up = false
          }
        } else {
          this.caoBaoWinJump.value -= 1
          if(this.caoBaoWinJump.value <= 0) {
            this.caoBaoWinJump.up = true
          }
        }
      } else {
        this.caobaoSize.h = this.wh / 3.5 * this.caobaoSize.scale
        this.caobaoSize.w = this.caobaoSize.h * 251.5 / 297.1
        this.caobaoPos.w = this.ww / 20,
        this.caobaoPos.h = this.wh - this.caobaoSize.h
      }

      // 處理無敵時間
      if (this.gamePanel.starTime > 0) {
        this.gamePanel.starTime -= 1000 / this.fps
      }

      this.caobaoRunnung += 0.02
      if(this.caobaoRunnung >= 1.97) {
        this.caobaoRunnung = 0
      }
      if(this.goal.length > 0) {
        this.goal.forEach(item => {
          // console.log(item.pos)
          if(!item.stopPos) {
            item.pos -= this.road.speed + item.speed
          } else if(item.pos + this.ww + this.ww * item.size / 100 / 2 > this.ww / 2) {
            item.pos -= this.road.speed + item.speed
          }
        })
      }
      // this.gamePanel.score++
    },
    draw() {
      this.drawBg()
      this.drawItems()
      this.drawCaoBao()
      this.drawFail()
      this.drawGoal()
      this.drawWin()
      window.requestAnimationFrame(this.draw)
    },
    drawBg() {
      this.ctx.save()
      // bg -sky
      this.ctx.drawImage(this.images.bg.sky, this.sky.pos1, 0, this.ww * 2, this.wh)
      this.ctx.drawImage(this.images.bg.sky, this.sky.pos2, 0, this.ww * 2, this.wh)
      // bg -city 4118 * 481
      this.ctx.drawImage(this.images.bg.city, this.city.pos1, (1080 - 432 - 454.14) / 1080 * this.wh, this.ww * 2, this.wh * 484.14 / 1080)
      this.ctx.drawImage(this.images.bg.city, this.city.pos2, (1080 - 432 - 454.14) / 1080 * this.wh, this.ww * 2, this.wh * 484.14 / 1080)
      // bg -cloud 972 * 395
      this.ctx.restore()

      this.ctx.save()
      this.ctx.globalAlpha = .6
      this.ctx.drawImage(this.images.bg.cloud, this.cloud.pos1, this.wh * .15, this.ww * .6, this.wh * .5)
      this.ctx.drawImage(this.images.bg.cloud, this.cloud.pos2, this.wh * .15, this.ww * .6, this.wh * .5)
      this.ctx.restore()

      this.ctx.save()
      // bg -road 1919 * 432
      this.ctx.drawImage(this.images.bg.road, this.road.pos1, (1080 - 432) / 1080 * this.wh, this.ww * 2, this.wh * 432 / 1080)
      this.ctx.drawImage(this.images.bg.road, this.road.pos2, (1080 - 432) / 1080 * this.wh, this.ww * 2, this.wh * 432 / 1080)

      this.ctx.restore()

      // game panel: time
      this.ctx.save()
      this.ctx.font = "4vmin Lexend Exa"
      this.ctx.fillStyle = "black"
      this.ctx.textAlign = "center"
      this.ctx.fillText(`TIME: ${this.gamePanel.showTime}s`, this.ww / 2, this.wh / 10)
      this.ctx.restore()

      this.ctx.save()
      // game panel: life
      for(let i = 0; i < this.gamePanel.life; i++) {
        // life 55.47* 65.97
        const lifeWidth = this.ww / 20
        const lifeHeight = 65.97 / 55.47 * lifeWidth
        this.ctx.drawImage(this.images.life, this.ww / 50 + i * lifeWidth * 1.2, this.wh / 75, lifeWidth, lifeHeight)
      }
      this.ctx.restore()

      // game panel: score
      this.ctx.save()
      const scoreWidth = this.ww / 20
      const scoreHeight = scoreWidth
      this.ctx.drawImage(this.images.score, this.ww * 40 / 50, this.wh / 50, scoreWidth, scoreHeight)
      this.ctx.font = "4vmin Lexend Exa"
      this.ctx.fillStyle = "black"
      this.ctx.textAlign = "start"
      const score = this.padLeft(this.gamePanel.score)
      this.ctx.fillText(`x${score}`, this.ww * 40 / 50 + scoreWidth * 1.3, this.wh / 10)
      this.ctx.restore()

      // game title
      if(this.gameStatus === 0) {
        // game title
        const tw = this.ww / 4
        const th = tw * 424 / 540
        const titlePos = {
          w: (this.ww - tw) * 7 / 10,
          h: (this.wh - th) * 4 / 10
        }
        this.ctx.drawImage(this.images.startText, titlePos.w, titlePos.h, tw, th)

        // game start button
        this.ctx.save()
        const buttonRadius = this. wh / 20
        const buttonWidth = tw * 3 / 5
        this.ctx.translate(titlePos.w + (tw - buttonRadius * 2 - buttonWidth) / 2, titlePos.h + th + buttonRadius)
        this.ctx.beginPath()
        this.ctx.fillStyle = '#FF7600'
        this.ctx.strokeStyle = 'black'
        this.ctx.lineWidth = 10
        this.ctx.moveTo(buttonRadius, 0)
        this.ctx.lineTo(buttonRadius + buttonWidth, 0)
        this.ctx.moveTo(buttonRadius, buttonRadius * 2)
        this.ctx.lineTo(buttonRadius + buttonWidth, buttonRadius * 2)
        this.ctx.arc(buttonRadius, buttonRadius, buttonRadius, Math.PI * 2 / 4, Math.PI * 2 * 3 / 4)
        this.ctx.arc(buttonRadius + buttonWidth, buttonRadius, buttonRadius, -Math.PI * 2 / 4, Math.PI * 2 / 4)
        this.ctx.closePath()
        this.ctx.stroke()
        this.ctx.fill()

        this.ctx.font = "4vmin Lexend Exa"
        this.ctx.fillStyle = "black"
        this.ctx.textAlign = "center"
        this.ctx.fillText('START', buttonRadius + buttonWidth / 2, buttonRadius * 1.5)
        this.ctx.font = "1.5vmin Lexend Exa"
        this.ctx.fillText('按 s 開始', buttonRadius + buttonWidth / 2, buttonRadius * 3)

        // hint
        this.ctx.drawImage(this.images.keyUp, buttonRadius, buttonRadius * 3.5, buttonRadius * 2, buttonRadius * 2)
        this.ctx.drawImage(this.images.keyDown, buttonRadius + buttonWidth * 2 / 3, buttonRadius * 3.5, buttonRadius * 2, buttonRadius * 2)
        this.ctx.restore()
      }

      // finish
      if(this.gameStatus === 3) {
        if(this.winEffect <= 100) {
          this.winEffect += 1
        }
      }
      // this.ctx.restore()
    },
    drawWin() {
      this.ctx.save()
      if(this.gameStatus === 3) {
        // this.ctx.globalAlpha = .3
        // console.log((this.winEffect / 100))
        this.ctx.globalAlpha = .25 + (this.winEffect / 100)
        const warningW = this.ww * 7 / 10
        const warningH = warningW / 1103.91 * 164.913
        const warningX =( this.ww - warningW) / 2
        const warningY = this.wh / 10 - (100 - this.winEffect)
        this.ctx.drawImage(this.images.warning, warningX, warningY, warningW, warningH)
        const ribbonW = this.ww * 4 / 10
        const ribbonH = ribbonW / 820.435 * 472.88
        const ribbonX = (this.ww - ribbonW) / 2
        const ribbonY = this.wh * 4 / 10 - (100 - this.winEffect) / 2
        this.ctx.drawImage(this.images.ribbon, ribbonX, ribbonY, ribbonW, ribbonH)

        // game restart button
        this.ctx.save()
        const buttonRadius = this.wh / 20
        const buttonWidth = this.ww / 6
        this.ctx.translate((this.ww - buttonWidth - buttonRadius) / 2, warningH * 1.2 + warningY)
        this.ctx.beginPath()
        this.ctx.fillStyle = '#FF7600'
        this.ctx.strokeStyle = 'black'
        this.ctx.lineWidth = 10
        this.ctx.moveTo(buttonRadius, 0)
        this.ctx.lineTo(buttonRadius + buttonWidth, 0)
        this.ctx.moveTo(buttonRadius, buttonRadius * 2)
        this.ctx.lineTo(buttonRadius + buttonWidth, buttonRadius * 2)
        this.ctx.arc(buttonRadius, buttonRadius, buttonRadius, Math.PI * 2 / 4, Math.PI * 2 * 3 / 4)
        this.ctx.arc(buttonRadius + buttonWidth, buttonRadius, buttonRadius, -Math.PI * 2 / 4, Math.PI * 2 / 4)
        this.ctx.closePath()
        this.ctx.stroke()
        this.ctx.fill()

        this.ctx.font = "3vmin Lexend Exa"
        this.ctx.fillStyle = "black"
        this.ctx.textAlign = "center"
        this.ctx.fillText('RESTART', buttonRadius + buttonWidth / 2, buttonRadius * 1.5)
        this.ctx.font = "1.5vmin Lexend Exa"
        this.ctx.fillText('按 r 重新開始', buttonRadius + buttonWidth / 2, buttonRadius * 3)
        this.ctx.restore()
      }
      this.ctx.restore()
    },
    drawFail() {
      // failed
      this.ctx.save()
      if(this.gameStatus === 4) {
        if(this.failEffect <= 100) {
          this.failEffect += 1
        }
        this.ctx.fillStyle = `rgba(0, 0, 0, ${this.failEffect / 50})`
        this.ctx.fillRect(0, 0, this.ww, this.wh)
        const tw = this.ww / 4
        const th = tw * 424 / 540
        const titlePos = {
          w: (this.ww - tw) * 5 / 10,
          h: (this.wh - th) * 3 * this.failEffect / 100 / 10
        }
        this.ctx.drawImage(this.images.gameover, titlePos.w, titlePos.h, tw, th)

        // game start button
        this.ctx.save()
        const buttonRadius = this. wh / 20
        const buttonWidth = tw * 3 / 5
        this.ctx.translate(titlePos.w + (tw - buttonRadius * 2 - buttonWidth) / 2, titlePos.h + th + buttonRadius)
        this.ctx.beginPath()
        this.ctx.fillStyle = '#FF7600'
        this.ctx.strokeStyle = 'white'
        this.ctx.lineWidth = 10
        this.ctx.moveTo(buttonRadius, 0)
        this.ctx.lineTo(buttonRadius + buttonWidth, 0)
        this.ctx.moveTo(buttonRadius, buttonRadius * 2)
        this.ctx.lineTo(buttonRadius + buttonWidth, buttonRadius * 2)
        this.ctx.arc(buttonRadius, buttonRadius, buttonRadius, Math.PI * 2 / 4, Math.PI * 2 * 3 / 4)
        this.ctx.arc(buttonRadius + buttonWidth, buttonRadius, buttonRadius, -Math.PI * 2 / 4, Math.PI * 2 / 4)
        this.ctx.closePath()
        this.ctx.stroke()
        this.ctx.fill()

        this.ctx.font = "3vmin Lexend Exa"
        this.ctx.fillStyle = "white"
        this.ctx.textAlign = "center"
        this.ctx.fillText('RESTART', buttonRadius + buttonWidth / 2, buttonRadius * 1.5)
        this.ctx.font = "1.5vmin Lexend Exa"
        this.ctx.fillText('按 r 重新開始', buttonRadius + buttonWidth / 2, buttonRadius * 3)
        this.ctx.restore()
      }
      this.ctx.restore()
    },
    drawGoal() {
      this.ctx.save()
      if(this.goal.length > 0) {
        this.goal.forEach(item => {
          switch (item.type) {
            case 'finish':
              this.ctx.drawImage(this.images.goal[item.type], this.ww + item.pos, this.wh * .55, this.ww * item.size / 100, this.ww * item.size / 100 / 612.149 * 435.515)
              break
            case 'fadacai':
              this.ctx.drawImage(this.images.goal[item.type], this.ww + item.pos, this.wh * .55, this.ww * item.size / 100, this.ww * item.size / 100 / 699.123 * 401.5)
              break
            default:
              break
          }
        })
      }
      this.ctx.restore()
    },
    padLeft(str) {
      const s = str + ''
      if(s.length >= 3) {
        return s
      } else {
        return this.padLeft('0' + s)
      }
    },
    drawCaoBao() {
      this.ctx.save()
      // win
      if(this.gameStatus === 3) {
        this.ctx.drawImage(this.images.caobaoWin, this.caobaoPos.w, this.caobaoPos.h * this.tracks[1] - this.caoBaoWinJump.value, this.caobaoSize.w, this.caobaoSize.h)
        return
      }

      const run = Math.floor(this.caobaoRunnung)
      // console.log('run', run)
      this.ctx.drawImage(this.images.caobao[run], this.caobaoPos.w, this.caobaoPos.h * this.tracks[this.caobaoPos.currentTrack], this.caobaoSize.w, this.caobaoSize.h)
      if (this.gamePanel.starTime > 0) {
        this.ctx.drawImage(this.images.starAurora, this.caobaoPos.w, this.caobaoPos.h * this.tracks[this.caobaoPos.currentTrack], this.caobaoSize.w, this.caobaoSize.h)
        this.ctx.drawImage(this.images.starShining, this.caobaoPos.w, this.caobaoPos.h * this.tracks[this.caobaoPos.currentTrack], this.caobaoSize.w, this.caobaoSize.h)
      }

      this.ctx.restore()
    },
    updateItems() {
      this.itemsInTracks.forEach((track, index) => {
        track.forEach((item) => {
          if(!item.crash) {
            item.pos -= this.road.speed + item.speed
          } else {
            item.pos -= this.road.speed + item.speed / 2
            item.crash -= 0.3
          }
          // console.log(this.currentTrack)
          if (item.danger && !item.die && !item.crash && item.pos + this.ww < this.caobaoSize.w + this.caobaoPos.w + this.ww / 20 && this.caobaoPos.currentTrack !== index) {
            item.die = true
          }
          if (!item.die && !item.crash && item.pos + this.ww < this.caobaoSize.w + this.caobaoPos.w && this.caobaoPos.currentTrack === index) {
            // console.log('crash')
            item.crash = 0.1
            if(item.type === 'gold') {
              this.gamePanel.score++
            }
            if(item.type === 'mushroom') {
              this.gamePanel.mushroom = true
              // if (this.caobaoSize.scale <= this.caobaoSize.big) {
              //   this.caobaoSize.scale += 0.1
              // }
              // this.caobaoSize.h = this.wh / 3.5 * this.caobaoSize.scale
              // this.caobaoSize.w = this.caobaoSize.h * 251.5 / 297.1
              // this.caobaoPos.w = this.ww / 20,
              // this.caobaoPos.h = this.wh - this.caobaoSize.h
            }
            if(item.type === 'star') {
              this.gamePanel.starTime = 10000
            }
            if(item.danger) {
              if (this.gamePanel.starTime > 0) {
                //
              } else if (this.gamePanel.mushroom) {
                this.gamePanel.mushroom = false
              } else {
                this.gamePanel.life--
              }
            }
          }
        })
      })
      this.clearItems()
    },
    putItem(track) {
      // let randomTrack = this.getRandomArbitrary(0, 3)
      const randomTrack = track
      // let items = [...this.dangersLv1, ...this.items]
      let items = []
      this.items.forEach(item => {
        for(let i = 1; i <= item.p; i++) {
          items.push(item)
        }
      })
      this.dangersLv1.forEach(item => {
        for(let i = 1; i <= item.p; i++) {
          items.push(item)
        }
      })
      if(this.gamePanel.showTime < 60) {
        this.dangersLv2.forEach(item => {
          for(let i = 1; i <= item.p; i++) {
            items.push(item)
          }
        })
      }
      if(this.gamePanel.showTime < 30) {
        this.dangersLv3.forEach(item => {
          for(let i = 1; i <= item.p; i++) {
            items.push(item)
          }
        })
      }
      let randomDanger = this.getRandomArbitrary(0, items.length)
      let item = Object.assign({}, items[randomDanger])
      // console.log(item)
      this.itemsInTracks[randomTrack].push(item)
    },
    getRandomArbitrary(min, max) {
      return Math.floor(Math.random() * max) + min
    },
    clearItems() {
      this.itemsInTracks.forEach((track, index ,object) => {
        track.forEach((item, i) => {
          if(item.crash <= -20) {
            object[index].splice(i, 1)
          }
          if(item.pos + this.ww * item.size / 100 < -this.ww) {
            object[index].splice(i, 1)
          }
        })
      })
    },
    drawItems() {
      this.itemsInTracks.forEach((track, index) => {
        // console.log(track)
        const rockTrack = [.64, .765, .87]
        const mosTrack = [.62, .75, .86]
        const guoDonTrack = [.45, .56, .67]
        const svg1450 = [.43, .56, .67]
        const floodingTrack = [.55, .67, .8]
        const goldTrack = [.6, .73, .84]
        const mushroomTrack = [.6, .73, .84]
        const starTrack = [.6, .73, .84]
        track.forEach((item) => {
          switch (item.type) {
            case 'rock':
              this.ctx.save()
              // console.log('rock', danger)
              this.ctx.drawImage(this.images.dangers[item.type], this.ww + item.pos, this.wh * rockTrack[index], this.ww * item.size / 100, this.ww * item.size / 100 / 74.38 * 60.19)
              this.ctx.restore()
              break
            case 'mosquito':
              this.ctx.save()
              this.ctx.drawImage(this.images.dangers[item.type], this.ww + item.pos, this.wh * mosTrack[index], this.ww * item.size / 100, this.ww * item.size / 100 / 116.97 * 142.64)
              this.ctx.drawImage(this.images.dangers[item.type], this.ww + item.pos + this.ww * item.size / 100 * 1.1, this.wh * mosTrack[index], this.ww * item.size / 100, this.ww * item.size / 100 / 116.97 * 142.64)
              this.ctx.restore()
              break
            case 'tsaiIngWen': // 265.568 * 299.873
              this.ctx.save()
              this.ctx.drawImage(this.images.dangers[item.type], this.ww + item.pos, this.wh * guoDonTrack[index], this.ww * item.size / 100, this.ww * item.size / 100 / 265.568 * 299.873)
              this.ctx.restore()
              break
            case 'guoDon':
              this.ctx.save()
              this.ctx.drawImage(this.images.dangers[item.type], this.ww + item.pos, this.wh * guoDonTrack[index], this.ww * item.size / 100, this.ww * item.size / 100 / 262.154 * 310.016)
              this.ctx.restore()
              break
            case 'svg1450':
              this.ctx.save()
              this.ctx.drawImage(this.images.dangers[item.type], this.ww + item.pos, this.wh * svg1450[index], this.ww * item.size / 100, this.ww * item.size / 100 / 200.855 * 318.707)
              this.ctx.restore()
              break
            case 'flooding':
              this.ctx.save()
              this.ctx.drawImage(this.images.dangers[item.type], this.ww + item.pos, this.wh * floodingTrack[index], this.ww * item.size / 100, this.ww * item.size / 100 / 820.706 * 267.4)
              this.ctx.restore()
              break
            case 'gold':
              this.ctx.save()
              if(item.crash) {
                this.ctx.globalAlpha = (20 + item.crash) / 20
                this.ctx.drawImage(this.images.items[item.type + 'Crash'], this.ww + item.pos, this.wh * (goldTrack[index] - 0.04) + item.crash, this.ww * (item.size + 3) / 100, this.ww * (item.size + 3) / 100 / 135.052 * 152.002)
              } else {
                this.ctx.drawImage(this.images.items[item.type], this.ww + item.pos, this.wh * goldTrack[index], this.ww * item.size / 100, this.ww * item.size / 100 / 86.123 * 86.123)
              }
              this.ctx.restore()
              break
            case 'mushroom':
              this.ctx.save()
              if(item.crash) {
                this.ctx.globalAlpha = (20 + item.crash) / 20
                this.ctx.drawImage(this.images.items[item.type + 'Crash'], this.ww + item.pos, this.wh * (mushroomTrack[index] - 0.04), this.ww * (item.size + 3) / 100, this.ww * (item.size + 3) / 100 / 117.608 * 98.783)
              } else {
                this.ctx.drawImage(this.images.items[item.type], this.ww + item.pos, this.wh * mushroomTrack[index], this.ww * item.size / 100, this.ww * item.size / 100 / 117.608 * 98.783)
              }
              this.ctx.restore()
              break
            case 'star':
              this.ctx.save()
              if(item.crash) {
                this.ctx.globalAlpha = (20 + item.crash) / 20
                this.ctx.drawImage(this.images.items[item.type + 'Crash'], this.ww + item.pos, this.wh * (starTrack[index] - 0.04), this.ww * (item.size + 3) / 100, this.ww * (item.size + 3) / 100 / 120.39 * 120.061)
              } else {
                this.ctx.drawImage(this.images.items[item.type], this.ww + item.pos, this.wh * starTrack[index], this.ww * item.size / 100, this.ww * item.size / 100 / 120.39 * 120.061)
              }
              this.ctx.restore()
              break
            default:
              break
          }
        })
      })
    },
    onKeyboardClick(e) {
      // console.log('keypress', e.keyCode)
      const keyCode = e.keyCode
      switch (keyCode) {
        case 38: // arrow up
          if(this.caobaoPos.currentTrack > 0) {
            this.caobaoPos.currentTrack--
          }
          break
        case 40: // arrow down
          if(this.caobaoPos.currentTrack < 2) {
            this.caobaoPos.currentTrack++
          }
          break
        case 83: // key 's'
          if(this.gameStatus === 2) return
          this.gameStatus = 2
          this.itemInterval = setInterval(() => {
            if((this.gamePanel.showTime * 10) % 2 === 0){
              let a = Math.random()
              if(a > .3) {
                // console.log(0)
                this.putItem(0)
              }
              a = Math.random()
              if(a > .3) {
                // console.log(1)
                this.putItem(1)
              }
              a = Math.random()
              if(a > .3) {
                // console.log(2)
                this.putItem(2)
              }
            }
          }, 700)
          break
        case 82: // key 'r'
          this.gameStatus = 0
          this.itemsInTracks = [
            [],
            [],
            []
          ]
          this.goal = []
          this.gamePanel = {...gamePanel}
          this.winEffect = 0
          this.failEffect = 0
          this.caobaoPos = {
            w: 0, h: 0, currentTrack: 1
          }
          break
        default:
          break
      }
    },
    getMousePos(evt) {
      if (!evt) return
      const rect = this.fadacai.getBoundingClientRect()
      this.mousePos = {
        x: evt.clientX - rect.left - this.ww / 2,
        y: evt.clientY - rect.top - this.wh / 2
      }
    },
    onMouseMove(evt) {
      if (!evt) return
      this.getMousePos(evt)
    }
  }
}
</script>

